import {
	getLogin,
} from '../../../service/api/login.js' //登陆api
var tha;
export default {
	onLoad(){
		tha = this;
	},
	data() {
		return {
			phoneno:'',
			password:''
		};
	},
	methods: {
	    bindLogin() {
			var _this=this
			// if (this.phoneno.length != 11) {
			//      uni.showToast({
			//         icon: 'none',
			// 		position: 'bottom',
			//         title: '手机号不正确'
			//     });
			//     return;
			// }
			if (this.phoneno.length == "") {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '用户名不能为空'
			    });
			    return;
			}
	        if (this.password.length < 5) {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码不正确'
	            });
	            return;
	        }
			uni.showLoading({
				title: '登陆中'
			});
			getLogin()
			.then(res => {
				//console.log(res)
				//简单验证下登陆
				if(_this.phoneno==res.data.username && _this.password==res.data.password){
					_this.$store.dispatch("setUserData",res.data)
					uni.showToast({
						icon: 'success',
						position: 'bottom',
						title: '登陆成功'
					});
					uni.reLaunch({
						url: '../../../pages/index',
					});
				}else{
					_this.password=""
					uni.showToast({
						icon: 'error',
						position: 'bottom',
						title: '账号或密码错误，账号admin密码admin'
					});
				}
				uni.hideLoading();
			}).catch(err => {
				uni.hideLoading();
			})
			
			
			
			
			
	    }
	}
}
